


// класс применяется ко всему тексту в горизонтальном скроле
.scrollGorizont__text{
  z-index: 60;
  width: 50%;
  margin-top: 20px;
  @media (max-width:1130px){
    margin: 20px 0 0 auto;
    width: 60%;
  }
  @media (max-width:800px){
    width: 70%;
    margin-top: 18px;
  }
  @media (max-width:700px){
    width: 88%;
    margin-top: 16px;
  }
  @media (max-width:600px){
    width: 95%;
  }
  .scrollGorizont__section_text_title{
    position: relative;
    font-family: "Gerhaus Regular";
    color: #fff;
    
    .scrollGorizont__section_text_title__top{
      .scrollGorizont__section_text_title__top_sub{
        position: absolute;
        left: -100%;
        top: 0;
        width: 100%;
        display: flex;
        justify-content: flex-end;
        h4{
          text-align: right;
          color: #fff;
          font-size: 36px;
          z-index: 30;
          margin-right: 10px;
          line-height: 90%;
          @media (max-width:820px){
            font-size: 27px;
          }
          @media (max-width:700px){
            display: none;
          }
        }
      }
      h3{
        font-size: 14px;
        margin-top: -15px;
        margin-bottom: 30px;
      }
    }
    .scrollGorizont__section_text_title__center{
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
      h5{
        font-size: 40px;
        color: #222;
        width: 100%;
        margin-bottom: 15px;
        font-weight: 700;
        span{
          text-decoration: underline;
        }
        @media (max-width:890px){
          font-size: 30px;
        }
      }
      p{
        width: 580px;
        margin-top: 20px;
        padding-left: 150px;
        font-size: 35px;
        
        @media (max-width:1130px){
          width: 99%;
        }
        @media (max-width:890px){
          font-size: 25px;
          padding-left: 80px;
        }
      }
    }
    .scrollGorizont__section_text_title__bottom{
      width: 600px;
      border-top: 1px solid #fff;
      margin-top: 10vh;
      padding-top: 20px;
      display: flex;
      justify-content: space-between;
      :not(:last-child){
        margin-right: 37px;
      }
      @media (max-width:1200px){
        width: 99%;
      }
      @media (max-width:890px){
        :not(:last-child){
          margin-right: 20px;
        }
      }
      h5{
        font-family: "HelveticaNeue";
        font-size: 14px;
        @media (max-width:820px){
          font-size: 12px;
        }
      }
    }    
  }
}
